export default class Button {
  text: string;
  icon: string;
  animation?: string;
  alternateIcon?: string;
  constructor(
    text: string,
    icon: string,
    alternateIcon?: string,
    animation?: string
  ) {
    this.text = text;
    this.icon = icon;
    this.alternateIcon = alternateIcon ? alternateIcon : null;
    this.animation = animation;
  }
}
