import { Pipe, PipeTransform } from '@angular/core';
import { isArray } from 'util';

@Pipe({
  name: 'general'
})
export class GeneralPipe implements PipeTransform {
  transform(value: any, args?: any): any {
    if (!args) {
      return value;
    }
    if (isArray(args)) {
      args.forEach(pipe => {
        value = (<PipeTransform>pipe).transform(value);
      });
    } else {
      value = (<PipeTransform>args).transform(value);
    }
    return value;
  }
}
